
/**
 * 숫자 문자(1-4)를 원문자 숫자 문자로 변환한다.
 * @type { (number: string) => string | undefined } 
 */
export function to_circled_number(number) {
    switch (number) {
        case '1':
            return '①';
        case '2':
            return '②';
        case '3':
            return '③';
        case '4':
            return '④';
        default:
            return undefined;
    }
}