<script>
  import "carbon-components-svelte/css/all.css";
  import {
    Content,
    Form,
    FormGroup,
    TileGroup,
    RadioTile,
    Header,
    SkipToContent,
    Tag,
    ToastNotification,
  } from "carbon-components-svelte";
  import { fly } from "svelte/transition";
  import OptionTile from "./lib/components/OptionTile.svelte";
  import { onMount } from "svelte";
  import { to_circled_number } from "./utils";

  let theme = "g100"; // "white" | "g10" | "g80" | "g90" | "g100"
  $: document.documentElement.setAttribute("theme", theme);

  let options = ["1", "2", "3", "4"];
  let answer;
  let selected_option;

  let states = {
    correct: {
      kind: "success",
      title: "정답",
      subtitle: "올바른 답을 선택하였습니다.",
    },
    incorrect: {
      kind: "error",
      title: "오답",
      subtitle: "다시 선택하세요.",
    },
  };
  let state;
  $: state = selected_option === answer ? states.correct : states.incorrect;

  onMount(async () => {
    // 필드 정보를 가져와 표시합니다.
    let _items = document.querySelectorAll('#data-deck > div[id^="data-"]');
    _items.forEach((element) => {
      if ("id" in element) {
        try {
          let _innerHTML = document.getElementById(element.id).innerHTML;
          let _id = element.id.substring(5);
          document.getElementById(_id).innerHTML = _innerHTML;
        } catch (e) {}
      }
    });

    // 정답을 가져옵니다.
    answer = document.getElementById("data-answer").innerText;
    document.getElementById("answer").innerText = to_circled_number(answer);
  });
</script>

<Header company="FFK" platformName="골든벨">
  <svelte:fragment slot="skip-to-content">
    <SkipToContent />
  </svelte:fragment>
</Header>

<Content>
  <!-- <div id="data-deck">
    <div id="data-question">질문</div>
    <div id="data-prompt">보기</div>
    <div id="data-option-1">Option 1</div>
    <div id="data-option-2">Option 2</div>
    <div id="data-option-3">Option 3</div>
    <div id="data-option-4">Option 4</div>
    <div id="data-answer">1</div>
    <div id="data-field">전공 분야</div>
    <div id="data-subject">농업의 이해</div>
    <div id="data-solution">해설</div>
  </div> -->
  <Form>
    <FormGroup>
      <div id="question" />
      <div id="prompt" />
    </FormGroup>
    <FormGroup>
      <TileGroup name="options" bind:selected={selected_option}>
        {#each options as option}
          <RadioTile value={option}>
            <OptionTile value={option}><div id="option-{option}" /></OptionTile>
          </RadioTile>
        {/each}
      </TileGroup>
    </FormGroup>
    <FormGroup>
      {#if selected_option !== undefined}
        <div transition:fly={{ delay: 0, duration: 300, axis: "y" }}>
          <ToastNotification
            kind={state.kind}
            role="status"
            title={state.title}
            subtitle={state.subtitle}
            lowContrast
            fullWidth
            on:close={() => {
              selected_option = undefined;
            }}
          />
        </div>
      {/if}
    </FormGroup>
    <div
      style={selected_option === answer
        ? "visibility: visible;"
        : "visibility: hidden;"}
    >
      <FormGroup legendText="정답">
        <div id="answer" />
      </FormGroup>
      <FormGroup legendText="관련 과목">
        <Tag type="green"><div id="field" /></Tag>
        <Tag type="cyan"><div id="subject" /></Tag>
      </FormGroup>
      <FormGroup legendText="해설">
        <div id="solution" />
      </FormGroup>
    </div>
  </Form>
</Content>

<style lang="scss">
  @use "@carbon/type";
  @use "@carbon/themes/scss/themes" as *;
  @use "@carbon/themes";

  #data-deck {
    display: none;
    visibility: hidden;
  }

  #question {
    @include type.type-style("body-01");
  }

  #prompt {
    margin-top: 1rem;
    padding: 1rem;
    border: 1px solid themes.$border-subtle-03;
  }
</style>
